import { useMemo, useRef, useState } from 'react'
import { ImageResize } from 'quill-image-resize-module-ts'
import { useNavigate } from 'react-router-dom'
import 'react-quill/dist/quill.snow.css'
import ReactQuill, { Quill } from 'react-quill'
import axios from 'axios'

Quill.register('modules/ImageResize', ImageResize)

export const WriteArticle = () => {
  let navigate = useNavigate()

  const [nickname, setNickname] = useState('')
  const [content, setContent] = useState('')
  const [title, setTitle] = useState('')
  const [password, setPassword] = useState('')

  const handleSubmit = async () => {
    const formData = new FormData()
    const doc = new DOMParser().parseFromString(content, 'text/html')

    //     
    const images = doc.getElementsByTagName('img')
    for (let i = 0; i < images.length; i++) {
      const imageSrc = images[i].src
      if (imageSrc.startsWith('data:')) {
        // MIME  '*/'    
        const mimeType = imageSrc.match(/data:([^;]+);/)[1]
        let extension = mimeType.split('/')[1] // '/'    
        if (extension === 'jpeg') {
          extension = 'jpg'
        }
        // Blob   FormData 
        const blob = await (await fetch(imageSrc)).blob()
        formData.append(`files`, blob, `image-${i}.${extension}`)
      }
      // URL      
    }

    Array.from(images).forEach((img, index) => {
      img.src = index.toString() //    src 
    })

    //  DOM  HTML   
    const htmlWithoutImages = doc.body.innerHTML

    //   HTML  FormData 
    formData.append('content', htmlWithoutImages)
    formData.append('nickname', nickname)
    formData.append('title', title)
    formData.append('password', password)

    //  FormData 
    try {
      const id = (
        await axios.post('http://loasim.com/api/article', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        })
      ).data.result
      navigate(`/article/${id}`)
    } catch (error) {
      console.error('Error uploading data:', error)
    }
  }

  // quill  
  // useMemo  modules      
  const modules = useMemo(() => {
    return {
      toolbar: {
        container: [
          [{ header: [1, 2, 3, false] }],
          ['bold', 'italic', 'underline', 'strike'],
          ['blockquote'],
          ['float', 'height', 'width'],
          [{ list: 'ordered' }, { list: 'bullet' }],
          [{ color: [] }, { background: [] }],
          [{ align: [] }, 'link', 'image'],
        ],
      },
      ImageResize: {
        parchment: Quill.import('parchment'),
        modules: ['Resize', 'DisplaySize'],
      },
      history: {
        delay: 500,
        maxStack: 100,
        userOnly: true,
      },
    }
  }, [])
  return (
    <>
      <div id="writeContain">
        <div id="writeHeader">
          <div>
            <input
              id="nicknameInput"
              className="headerInput"
              placeholder=""
              onChange={(event) => setNickname(event.target.value)}
            ></input>
            <input
              id="passwordInput"
              className="headerInput"
              placeholder=""
              onChange={(event) => setPassword(event.target.value)}
            ></input>
          </div>
          <input
            id="titleInput"
            className="headerInput"
            placeholder=""
            onChange={(event) => setTitle(event.target.value)}
          ></input>
        </div>
        <ReactQuill
          className="react-quill"
          placeholder="Quill Content"
          theme="snow"
          value={content}
          onChange={setContent}
          modules={modules}
        />
      </div>
      <div id="submitBtnContain">
        <div id="submit" onClick={handleSubmit}>
          
        </div>
      </div>
    </>
  )
}
